.pb-testimonial-wrapper {
  transition: all 0.3s ease-in-out;
  position: relative;
  /** Designs **/ }
  .pb-testimonial-wrapper:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0; }
  .pb-testimonial-wrapper .pb-testimonial--conr {
    transition: all 0.3s ease-in-out;
    position: relative; }
    .pb-testimonial-wrapper .pb-testimonial--conr::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      margin: 0 auto;
      bottom: -15px;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-top: 15px solid transparent;
      width: 0;
      height: 0;
      transition: all .3s ease-in-out; }
  .pb-testimonial-wrapper .pb-testimonial--details {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    position: relative; }
    .pb-testimonial-wrapper .pb-testimonial--details.image-position-top {
      flex-direction: column; }
      .pb-testimonial-wrapper .pb-testimonial--details.image-position-top .pb-testimonial-image {
        margin-bottom: 5px; }
    .pb-testimonial-wrapper .pb-testimonial--details.image-position-right {
      flex-direction: row-reverse; }
      .pb-testimonial-wrapper .pb-testimonial--details.image-position-right .pb-testimonial-image {
        margin-left: 5px; }
    .pb-testimonial-wrapper .pb-testimonial--details.image-position-bottom {
      flex-direction: column-reverse; }
      .pb-testimonial-wrapper .pb-testimonial--details.image-position-bottom .pb-testimonial-image {
        margin-top: 5px; }

